import React from 'react';
import _ from 'lodash';
import PropTypes from 'prop-types';

import Lineage from './lineage';
// import Lineage from './lineage1';

const adapter = (data) => {
  return {
    ...data,
    nodes: _.get(data, 'nodes', []).map(table => {
      return {
        ...table,
        title: table.name,
        isHide: table.isFold,
        isUnRelItemHidden: table.isUnRelColHide,
        nodeItems: table.columns.map(column => {
          return {
            ...column,
            id: [table.id, column.id || ''].join('@')
          }
        }),
      }
    }),
    edges: _.get(data, 'links', []).map(relation => {
      const {relU, u = '', relV, v = ''} = relation;
      return {
        ...relation,
        id: relation.id || [relU, u || '', relV, v || ''].join('@'),
        srcNodeItemId: u ? [relU, u].join('@') : relU,
        tgtNodeItemId: v ? [relV, v].join('@') : relV,
        label: '1111',
      }
    })
  }
}

// 
const Table = (props) => {
  const p = adapter(props);

  return (
    <Lineage {...p} />
  );
};

Table.propTypes = {
  options: PropTypes.object,
  layout: {
    ranksep: PropTypes.number,                      // 
    nodesep: PropTypes.number,                      // 
  },
  nodes: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string,                           //  ID
    icon: PropTypes.any,                            //  icon
    name: PropTypes.string,                         // 
    isFold: PropTypes.bool,                         // 
    isUnRelColHidden: PropTypes.bool,               // 
    columns: PropTypes.arrayOf(PropTypes.shape({
      name: PropTypes.string,                       // 
      icon: PropTypes.string,                       // icon
      title: PropTypes.string,                      // 
      onClick: PropTypes.func                       // 
    })),
    operators: PropTypes.arrayOf(                   
      PropTypes.shape({
        component: PropTypes.element                // 
      })
    ),    
  })),
  links: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string,                           // ID
    relU: PropTypes.string,                   // ID
    relV: PropTypes.string,                   // ID
    u: PropTypes.string,              // 
    v: PropTypes.string,              // 
    label: PropTypes.string || PropTypes.func, 
  })),
  onTableDoubleClick: PropTypes.func,               // 
  onLoaded: PropTypes.func,                         // 
  onEachFrame: PropTypes.func,                      // 
  onItemClick: PropTypes.func,                      //  
  // isLoop: PropTypes.boolean,
}

export default Table;
