import PropTypes from 'prop-types';

// 
export const NodeTypes = PropTypes.shape({
  id: PropTypes.string,                  // ID
  icon: PropTypes.string,                // 
  isHide: PropTypes.bool,                // 
  isUnRelItemHidden: PropTypes.bool,     // ()
  title: PropTypes.string,               // node
  nodeItems: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,              // ID
      level: PropTypes.number,           // 
      icon: PropTypes.string,            // 
      title: PropTypes.string,           // 
      onClick: PropTypes.func,           // 
    })
  ),
  operators: PropTypes.arrayOf(
    PropTypes.shape({
      compnent: PropTypes.element
    })
  )
});

// 
export const EdgeTypes = PropTypes.shape({
  srcNodeItemId: PropTypes.string,       // itemid
  tgtNodeItemId: PropTypes.string,       // itemid
  id: PropTypes.string,                  // ID
});
